<template lang="">
    <div>
        <hr>
        <p>ini halaman childs</p>
        <h5>update second from parent : {{secondchild}}</h5>
        <!-- <h5>update datachild dari child update: {{datachild}}</h5> -->
        <button @click="updateParentData">Update Parent Data</button>
    </div>
</template>

<script>
export default {
    name: "ParentChildTime",
    data() {
        return {
            datachild: 0,
            a: 0,
        };
    },
    methods: {
        updateParentData() {
            // Emit an event named 'updateParent' with some data
            this.$emit('updateParent', 'Data from child');
        }
    },
    emits: ['updateParent'],  // Declare the custom event

    props: {
        secondchild: Number,
    },

    updated() {

        this.a = this.a + 10
        console.log(this.a)
        this.datachild = this.secondchild
    },



}
</script>
