<template lang="">
    <div>
        <p>Realtime sec: {{ timed }}</p>
        <button @click="timed = timed + 1 ">Update Child Data</button>
        <p>Parent Data: {{ parentData }}</p>
        <parentchildtime @updateParent="handleUpdateParent" :secondchild="timed"></parentchildtime>
    </div>
</template>
<script>
import ParentChildTime from "@/components/Testing/ParentChildComponent/ParentChildTime.vue";
export default {
    name: "ParentChildTimeView",
    components: {
        parentchildtime: ParentChildTime,
    },
    data() {
        return {
            timed: 0,
            parentData: 'Initial data from parent'
        };
    },

    methods: {
        handleUpdateParent(dataFromChild) {
            // Update parentData with the data received from the child
            this.parentData = dataFromChild;
        },

        runingtime() {
            // Update parentData with the data received from the child
            var a = (new Date().getSeconds());
            console.log(a)
        }
    },

    updated() {
        console.log(this.parentData)
    },
    mounted() {
        // Update the time every second
        this.runingtime();
        this.interval = setInterval(this.runingtime, 1000);
        console.log(this.interval)
        // this.runingtime();
    },

    unmounted() {
        // Clear the interval when the component is destroyed
        clearInterval(this.interval);
        console.log(this.interval)
    },

}
</script>
