<template lang="">
    <div>
        <p>Parent Data: {{ parentData }}</p>
        <childside @updateParent="handleUpdateParent"></childside>
    </div>
</template>
<script>
import ChildSide from "@/components/Testing/ParentChildComponent/ParentChild.vue";
export default {
    name: "ParentChild",
    components: {
        childside: ChildSide,
    },
    data() {
        return {
            parentData: 'Initial data from parent'
        };
    },
    methods: {
        handleUpdateParent(dataFromChild) {
            // Update parentData with the data received from the child
            this.parentData = dataFromChild;
        }
    }

}
</script>
