<template lang="">
    <div>
        <button @click="updateParentData">Update Parent Data</button>
    </div>
</template>

<script>
export default {
    name: "ParentChild",
    methods: {
        updateParentData() {
            // Emit an event named 'updateParent' with some data
            this.$emit('updateParent', 'Data from child');
        }
    },
    emits: ['updateParent']  // Declare the custom event
}
</script>
